<template>
	<div class="course">
		<!--  -->
		<div class="btns">
			<el-button size='mini' @click='toAddCourse'></el-button>
			<el-button size='mini' @click='batchDeleteCourses'></el-button>
		</div>
		<!--  -->
		<!--  -->
		<div v-loading='loading'>
			<el-table :border='true' size='mini' :data="courses" style="width: 100%" @selection-change="handleSelectionChange">
				<el-table-column type="selection" width="55" fixed></el-table-column>
	      <el-table-column prop="name" label="" width="160"></el-table-column>
	      <el-table-column prop="period" label="" width="160"></el-table-column>
	      <el-table-column prop="description" label="" ></el-table-column>
	      <el-table-column label="" width='70' fixed="right" align='center'>
	      	<template slot-scope='{row}'>
	      		<i class="fa fa-trash" @click='deleteCourse(row.id)'></i>
	      		<i class="fa fa-pencil" @click='toUpdateCourse(row)'></i>
	      	</template>
	      </el-table-column>
	    </el-table>
		</div>
		<!--  -->
		<!--  -->
		<el-dialog :title="courseDialog.title" :visible.sync="courseDialog.visible">
			<!-- {{courseDialog.form}} -->
		  <el-form ref="courseForm" :rules='rules' :model="courseDialog.form" label-position='left' size='small'>
		    <el-form-item label="" label-width="100px" prop="name">
		      <el-input v-model="courseDialog.form.name" autocomplete="off"></el-input>
		    </el-form-item>
		    <el-form-item label="" label-width="100px" prop="period">
		      <el-input v-model="courseDialog.form.period" autocomplete="off"></el-input>
		    </el-form-item>
				<el-form-item label="" label-width="100px">
		      <el-input
					  type="textarea"
					  :rows="2"
					  placeholder=""
					  v-model="courseDialog.form.description">
					</el-input>
		    </el-form-item>
		  </el-form>
		  <div slot="footer" class="dialog-footer">
		    <el-button size='mini' @click='closeCourseDialog'> </el-button>
		    <el-button size='mini' type="primary" @click='saveOrUpdateCourse'> </el-button>
		  </div>
		</el-dialog>
		<!--  -->

	</div>
</template>
<script>
	import getAxios from '@/http/getAxios'
	let axios = getAxios();
	export default {
		data(){
			return {
				loading:false,
				courses:[],
				rules:{
					name:[{
						required: true, 
						message: '',
						trigger: 'blur' 
					}],
					period:[{
						required: true, 
						message: '',
						trigger: 'blur' 
					}]
				},
				multipleSelection:[],
				courseDialog:{
					title:'',
					visible:false,
					form:{}
				}
			}
		},
		created(){
			this.findAllCourses();
		},
		methods:{
			findAllCourses(){
				this.loading = true;
				axios.get('/course/findAllCourse')
				.then(({data:result})=>{
					this.courses = result.data;
				})
				.finally(()=>{
					this.loading = false;
				});
			},
			toAddCourse(){
				this.courseDialog.title = '';
				this.courseDialog.visible = true;
			},
			closeCourseDialog(){
				this.courseDialog.visible = false;
				this.courseDialog.form = {};
				this.$refs.courseForm.resetFields();
			},
			saveOrUpdateCourse(){
				this.$refs.courseForm.validate((valid)=>{
					if(valid){
						axios.post('/course/saveOrUpdate',this.courseDialog.form)
						.then(({data:result})=>{
							this.findAllCourses();
							this.closeCourseDialog();
							this.$notify.success({title:'',message:result.message})
						})
						.catch(()=>{
							this.$notify.error({title:'',message:''})
						})
					}
				});
			},
			toUpdateCourse(row){
				this.courseDialog.title = '';
				this.courseDialog.visible = true;
				this.courseDialog.form = row;
			},
			batchDeleteCourses(){
				this.$confirm('?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(()=>{
        	let ids = this.multipleSelection.map((item)=>{
						return item.id;
					})
					axios.post('/course/batchDelete',{ids})
					.then(({data:result})=>{
						this.findAllCourses();
						this.$notify.success({title:'',message:result.message})
					})
					.catch(()=>{
						this.$notify.error({title:'',message:''})
					})
        })
			},
			handleSelectionChange(val){
				this.multipleSelection = val;
			},
			deleteCourse(id){
				this.$confirm('?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(()=>{
        	axios.get('/course/deleteById?id='+id)
					.then(({data:result})=>{
						this.findAllCourses();
						this.$notify.success({title:'',message:result.message})
					})
					.catch(()=>{
						this.$notify.error({title:'',message:''})
					})
        })
			}
		}
	}
</script>
<style>
	.btns {
		margin-bottom: .5em;
	}
	i.fa {
		padding: 0 .3em;
		cursor: pointer;
	}
	i.fa.fa-trash {
		color: #F56C6C;
	}
</style>


