<!--  -->
<template>
	<div class="survey">
		<!--  -->
		<div class="btns">
		</div>
		<!--  -->
		<!--  -->
		<div v-loading='loading'>
			<el-table :border='true' size='mini' :data="surveys" style="width: 100%" >
				<el-table-column type="selection" width="55" fixed></el-table-column>
	      <el-table-column prop="clazzVM.grade.name" label="" width="100"></el-table-column>
	      <el-table-column prop="clazzVM.name" label="" width="100"></el-table-column>
	      <el-table-column prop="course.name" label="" width="100"></el-table-column>
	      <el-table-column prop="user.name" label="" width="80"></el-table-column>
	      <el-table-column prop="qnVM.name" label="" width="300"></el-table-column>
	      <el-table-column prop="surveydate" label="" width="160"></el-table-column>
	      <el-table-column prop="status" label="" width="80" align='center'></el-table-column>
	      <el-table-column label="" width='70' fixed="right" align='center'>
	      	<template slot-scope='{row}'>
	      		<a href="javascript:void(0)"></a>
	      	</template>
	      </el-table-column>
	    </el-table>
		</div>
		<!--  -->
		<!--  -->
		<el-dialog :title="surveyDialog.title" :visible.sync="surveyDialog.visible">
			<!-- {{surveyDialog.form}} -->
		  <el-form ref="surveyForm" :rules='rules' :model="surveyDialog.form" label-position='left' size='small'>
		    <el-form-item label="" label-width="100px" prop="name">
		      <el-input v-model="surveyDialog.form.name" autocomplete="off"></el-input>
		    </el-form-item>
				<el-form-item label="" label-width="100px" prop="descriptioin">
		      <el-input
					  type="textarea"
					  :rows="2"
					  placeholder=""
					  v-model="surveyDialog.form.descriptioin">
					</el-input>
		    </el-form-item>
		  </el-form>
		  <div slot="footer" class="dialog-footer">
		    <el-button size='mini' @click='closeSurveyDialog'> </el-button>
		    <el-button size='mini' type="primary" @click='saveOrUpdateSurvey'> </el-button>
		  </div>
		</el-dialog>
		<!--  -->

	</div>
</template>
<script>
	import getAxios from '@/http/getAxios'
	let axios = getAxios();
	export default {
		data(){
			return {
				loading:false,
				surveys:[],
				rules:{
				},
				multipleSelection:[],
				surveyDialog:{
					title:'',
					visible:false,
					form:{}
				}
			}
		},
		created(){
			this.findAllSurveys();
		},
		methods:{
			findAllSurveys(){
				this.loading = true;
				axios.get('/survey/findAllSurveyVM')
				.then(({data:result})=>{
					this.surveys = result.data;
				})
				.finally(()=>{
					this.loading = false;
				});
			},
			toAddSurvey(){
				this.surveyDialog.title = '';
				this.surveyDialog.visible = true;
			},
			closeSurveyDialog(){
				this.surveyDialog.visible = false;
				this.surveyDialog.form = {};
				this.$refs.surveyForm.resetFields();
			},
			saveOrUpdateSurvey(){
				this.$refs.surveyForm.validate((valid)=>{
					if(valid){
						axios.post('/survey/saveOrUpdateSurvey',this.surveyDialog.form)
						.then(({data:result})=>{
							this.findAllSurveys();
							this.closeSurveyDialog();
							this.$notify.success({title:'',message:result.message})
						})
						.catch(()=>{
							this.$notify.error({title:'',message:''})
						})		
					}
				});
			}
		}
	}
</script>
<style>
	.btns {
		margin-bottom: .5em;
	}
	i.fa {
		padding: 0 .3em;
		cursor: pointer;
	}
	i.fa.fa-trash {
		color: #F56C6C;
	}
</style>


