<template>
	<div class="user">
		<!--  -->
		<div class="btns">
			<el-button size='mini' @click='toAddUser'></el-button>
			<el-button size='mini' @click='batchDeleteUsers'></el-button>
		</div>
		<!--  -->
		<!--  -->
		<div v-loading='loading'>
			<el-table :border='true' size='mini' :data="users" style="width: 100%" @selection-change="handleSelectionChange">
				<el-table-column type="selection" width="55" fixed></el-table-column>
	      <el-table-column prop="name" label="" width="160"></el-table-column>
	      <el-table-column prop="gender" label="" width="80" align='center'></el-table-column>
	      <el-table-column prop="type" label="" width="80" align='center'></el-table-column>
	      <el-table-column prop="birth" label="" align='center'></el-table-column>
	      <el-table-column prop="hiredate" label="" align='center'></el-table-column>
	      <el-table-column label="" width='70' fixed="right" align='center'>
	      	<template slot-scope='{row}'>
	      		<i class="fa fa-trash" @click='deleteUser(row.id)'></i>
	      		<i class="fa fa-pencil" @click='toUpdateUser(row)'></i>
	      	</template>
	      </el-table-column>
	    </el-table>
		</div>
		<!--  -->
		<!--  -->
		<el-dialog :title="userDialog.title" :visible.sync="userDialog.visible">
		  <el-form ref="userForm" :rules='rules' :model="userDialog.form" label-position='left' size='small'>
		    <el-form-item label="" label-width="100px" prop="name">
		      <el-input v-model="userDialog.form.name" autocomplete="off"></el-input>
		    </el-form-item>
				<el-form-item label="" label-width="100px" prop="type">
		       <el-select style='width:100%' v-model="userDialog.form.type" placeholder="">
		        <el-option label="" value=""></el-option>
		        <el-option label="" value=""></el-option>
		      </el-select>
		    </el-form-item>
		    <el-form-item label="" label-width="100px">
		      <el-radio-group v-model="userDialog.form.gender">
				    <el-radio label=""></el-radio>
				    <el-radio label=""></el-radio>
				  </el-radio-group>
		    </el-form-item>
		    <el-form-item label="" label-width="100px">
		       <el-date-picker
			      v-model="userDialog.form.birth"
			      type="date"
			      value-format="yyyy-MM-dd"
			      placeholder="">
			    </el-date-picker>
		    </el-form-item>
		    <el-form-item label="" label-width="100px">
		      <el-date-picker
			      v-model="userDialog.form.hiredate"
			      type="date"
			      value-format="yyyy-MM-dd"
			      placeholder="">
			    </el-date-picker>
		    </el-form-item>
		  </el-form>
		  <div slot="footer" class="dialog-footer">
		    <el-button size='mini' @click='closeUserDialog'> </el-button>
		    <el-button size='mini' type="primary" @click='saveOrUpdateUser'> </el-button>
		  </div>
		</el-dialog>
		<!--  -->

	</div>
</template>
<script>
	import getAxios from '@/http/getAxios'
	let axios = getAxios();
	export default {
		data(){
			return {
				loading:false,
				users:[],
				rules:{
					name:[{
						required: true, 
						message: '',
						trigger: 'blur' 
					}]
				},
				multipleSelection:[],
				userDialog:{
					title:'',
					visible:false,
					form:{
						gender:'',
						type:''
					}
				}
			}
		},
		created(){
			this.findAllUsers();
		},
		methods:{
			findAllUsers(){
				this.loading = true;
				axios.get('/user/findAll')
				.then(({data:result})=>{
					this.users = result.data;
				})
				.finally(()=>{
					this.loading = false;
				});
			},
			toAddUser(){
				this.userDialog.title = '';
				this.userDialog.visible = true;
			},
			closeUserDialog(){
				this.userDialog.visible = false;
				this.userDialog.form = {};
				this.$refs.userForm.resetFields();
			},
			saveOrUpdateUser(){
				this.$refs.userForm.validate((valid)=>{
					if(valid){
						axios.post('/user/saveOrUpdate',this.userDialog.form)
						.then(({data:result})=>{
							this.findAllUsers();
							this.closeUserDialog();
							this.$notify.success({title:'',message:result.message})
						})
						.catch(()=>{
							this.$notify.error({title:'',message:''})
						})
					}
				})
			},
			toUpdateUser(row){
				this.userDialog.title = '';
				this.userDialog.visible = true;
				this.userDialog.form = row;
			},
			batchDeleteUsers(){
				this.$confirm('?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(()=>{
        	let ids = this.multipleSelection.map((item)=>{
						return item.id;
					})
					axios.post('/user/batchDelete',{ids})
					.then(({data:result})=>{
						this.findAllUsers();
						this.$notify.success({title:'',message:result.message})
					})
					.catch(()=>{
						this.$notify.error({title:'',message:''})
					})
        })
			},
			handleSelectionChange(val){
				this.multipleSelection = val;
			},
			deleteUser(id){
				this.$confirm('?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(()=>{
        	axios.get('/user/deleteById?id='+id)
					.then(({data:result})=>{
						this.findAllUsers();
						this.$notify.success({title:'',message:result.message})
					})
					.catch(()=>{
						this.$notify.error({title:'',message:''})
					})
        })
			}
		}
	}
</script>
<style>
	.btns {
		margin-bottom: .5em;
	}
	i.fa {
		padding: 0 .3em;
		cursor: pointer;
	}
	i.fa.fa-trash {
		color: #F56C6C;
	}
</style>


