<!--  -->
<template>
	<div class="survey">
		<!--  -->
		<div class="btns">
			<el-button size='mini' @click='toAddSurvey'></el-button>
			<el-button size='mini' @click='batchDeleteSurveys'></el-button>
		</div>
		<!--  -->
		<!--  -->
		<!-- {{surveys}} -->
		<div v-loading='loading'>
			<el-table :border='true' size='mini' :data="surveys" style="width: 100%" @selection-change="handleSelectionChange">
				<el-table-column type="selection" width="55" fixed></el-table-column>
	      <el-table-column prop="clazzVM.grade.name" label="" width="100"></el-table-column>
	      <el-table-column prop="clazzVM.name" label="" width="100"></el-table-column>
	      <el-table-column prop="course.name" label="" width="100"></el-table-column>
	      <el-table-column prop="user.name" label="" width="80"></el-table-column>
	      <el-table-column prop="qnVM.name" label="" width="300"></el-table-column>
	      <el-table-column prop="surveydate" label="" width="160"></el-table-column>
	      <el-table-column prop="status" label="" width="80" align='center'></el-table-column>
	      <el-table-column label="" width='100' fixed="right" align='center'>
	      	<template slot-scope='{row}'>
	      		<i class="fa fa-trash" @click='deleteSurvey(row.id)'></i>
	      		<i class="fa fa-pencil" @click='toUpdateSurvey(row)'></i>
	      		<el-button :disabled='row.status==""' type='text' @click='startSurvey(row)' >
	      			<i class="fa fa-power-off"></i>
	      		</el-button>
	      	</template>
	      </el-table-column>
	    </el-table>
		</div>
		<!--  -->
		<!--  -->
		<el-dialog :title="surveyDialog.title" :visible.sync="surveyDialog.visible">
			{{surveyDialog.form}}
		  <el-form ref="surveyForm" :rules='rules' :model="surveyDialog.form" label-position='left' size='small'>
		    <el-form-item label="" label-width="80px">
		      <el-select style='width:100%' v-model='surveyDialog.form.clazzId'>
		      	<el-option :key='v.id' :label='v.name' :value='v.id' v-for='v in clazzes'></el-option>
		      </el-select>
		    </el-form-item>
				<el-form-item label="" label-width="80px" >
		       <el-select style='width:100%' v-model='surveyDialog.form.courseId'>
		      	<el-option :key='v.id' :label='v.name' :value='v.id' v-for='v in courses'></el-option>
		      </el-select>
		    </el-form-item>
		    <el-form-item label="" label-width="80px" >
		      <el-select style='width:100%' v-model='surveyDialog.form.questionnaireId'>
		      	<el-option :key='v.id' :label='v.name' :value='v.id' v-for='v in questionnaires'></el-option>
		      </el-select>
		    </el-form-item>
		    <el-form-item label="" label-width="80px" >
		      <el-select style='width:100%' v-model='surveyDialog.form.userId'>
		      	<el-option :key='v.id' :label='v.name' :value='v.id' v-for='v in users'></el-option>
		      </el-select>
		    </el-form-item>
		  </el-form>
		  <div slot="footer" class="dialog-footer">
		    <el-button size='mini' @click='closeSurveyDialog'> </el-button>
		    <el-button size='mini' type="primary" @click='saveOrUpdateSurvey'> </el-button>
		  </div>
		</el-dialog>
		<!--  -->

	</div>
</template>
<script>
	import getAxios from '@/http/getAxios'
	let axios = getAxios();
	export default {
		data(){
			return {
				loading:false,
				surveys:[],
				rules:{
				},
				multipleSelection:[],
				surveyDialog:{
					title:'',
					visible:false,
					form:{}
				},
				//-----
				clazzes:[],
				courses:[],
				users:[],
				questionnaires:[]
			}
		},
		created(){
			this.findAllSurveys();

			this.findAllClazzes();
			this.findAllCourses();
			this.findAllQuestionnaires();
			this.findAllUsers();
		},
		methods:{
			startSurvey(row){
				axios.get('/survey/beginSurvey?id='+row.id)
				.then(({data:result})=>{
					this.findAllSurveys();
					this.$notify.success({title:'',message:result.message})
				})
				.catch(()=>{
					this.$notify.error({title:'',message:''})
				})
			},
			findAllSurveys(){
				this.loading = true;
				axios.get('/survey/findAllSurveyVM')
				.then(({data:result})=>{
					this.surveys = result.data;
				})
				.finally(()=>{
					this.loading = false;
				});
			},
			toAddSurvey(){
				this.surveyDialog.title = '';
				this.surveyDialog.visible = true;
			},
			closeSurveyDialog(){
				this.surveyDialog.visible = false;
				this.surveyDialog.form = {};
				this.$refs.surveyForm.resetFields();
			},
			saveOrUpdateSurvey(){
				this.$refs.surveyForm.validate((valid)=>{
					if(valid){
						axios.post('/survey/saveOrUpdateSurvey',this.surveyDialog.form)
						.then(({data:result})=>{
							this.findAllSurveys();
							this.closeSurveyDialog();
							this.$notify.success({title:'',message:result.message})
						})
						.catch(()=>{
							this.$notify.error({title:'',message:''})
						})		
					}
				});
			},
			toUpdateSurvey(row){
				this.surveyDialog.title = '';
				this.surveyDialog.visible = true;
				let survey = _.clone(row);
				survey.courseId = survey.course.id;
				survey.clazzId = survey.clazzVM.id;
				survey.userId = survey.user.id;
				survey.questionnaireId = survey.qnVM.id;

				delete survey.course;
				delete survey.clazzVM;
				delete survey.user;
				delete survey.qnVM;

				this.surveyDialog.form = survey;
			},
			batchDeleteSurveys(){
				this.$confirm('?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(()=>{
        	let ids = this.multipleSelection.map((item)=>{
						return item.id;
					})
					axios.post('/survey/batchDeleteSurveyById',{ids})
					.then(({data:result})=>{
						this.findAllSurveys();
						this.$notify.success({title:'',message:result.message})
					})
					.catch(()=>{
						this.$notify.error({title:'',message:''})
					})
        })
			},
			handleSelectionChange(val){
				this.multipleSelection = val;
			},
			deleteSurvey(id){
				this.$confirm('?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(()=>{
        	axios.get('/survey/deleteSurveyById?id='+id)
					.then(({data:result})=>{
						this.findAllSurveys();
						this.$notify.success({title:'',message:result.message})
					})
					.catch(()=>{
						this.$notify.error({title:'',message:''})
					})
        })
			},
			//--------
			findAllClazzes(){
				axios.get('/clazz/findAllVM')
				.then(({data:result})=>{
					this.clazzes = result.data;
				})
			},
			findAllCourses(){
				axios.get('/course/findAllCourse')
				.then(({data:result})=>{
					this.courses = result.data;
				})
			},
			findAllUsers(){
				axios.get('/user/findAll')
				.then(({data:result})=>{
					this.users = result.data;
				});
			},
			findAllQuestionnaires(){
				axios.get('/questionnaire/findAllQuestionnaire')
				.then(({data:result})=>{
					this.questionnaires = result.data;
				});
			}
		}
	}
</script>
<style>
	.btns {
		margin-bottom: .5em;
	}
	i.fa {
		padding: 0 .3em;
		cursor: pointer;
	}
	i.fa.fa-trash {
		color: #F56C6C;
	}
</style>


