<template>
	<div class="question-content">
		<!--  -->
		<div class="btns">
			<el-button size='mini' @click='toAddQuestion'></el-button>
			<el-button size='mini' @click='batchDeleteQuestion'></el-button>
		</div>
		<!--  -->
		<!--  -->
		<!-- {{ids}} -->
		<ul class="question-list" v-loading='loading'>
			<!--  -->
			<li class="question" v-for='q in questions'>
				<div class="question-title">
					<el-checkbox v-model='ids' :label='q.id'>{{q.name}}</el-checkbox>
					<el-tag type="warning" size='mini'>{{q.questionType}}</el-tag>
				</div>
				<div class="question-options">
					<ol>
						<li v-for='o in q.options'>
							<span>{{o.label}}:</span>
							<span>{{o.name}}</span>
						</li>
					</ol>
				</div>
				<div class="question-btns">
					<i class="fa fa-trash" @click='deleteQuestion(q.id)'></i>
					<i class="fa fa-pencil" @click='toUpdateQuestion(q)'></i>
				</div>
			</li>
			<!--  -->
		</ul>
		<!--  -->
		<!--  -->
		<el-dialog width='80%' :title="questionDialog.title" :visible.sync="questionDialog.visible">
			<!-- {{questionDialog.form}} -->
		  <el-form ref="questionForm" :rules='rules' :model="questionDialog.form" label-position='left' size='small'>
		    <el-form-item label="" label-width="100px" prop="name">
		       <el-input
						  type="textarea" :rows="2"
						  placeholder=""
						  v-model="questionDialog.form.name">
						</el-input>
		    </el-form-item>
				<el-form-item label="" label-width="100px" prop="descriptioin">
		     	<el-select v-model='questionDialog.form.questionType'>
		     		<el-option label='' value=''></el-option>
		     		<el-option label='' value=''></el-option>
		     		<el-option label='' value=''></el-option>
		     	</el-select>
		    </el-form-item>
		    <el-form-item v-if='questionDialog.form.questionType!=""' label="" label-width="100px" prop="descriptioin">
		    	<el-table :data='questionDialog.form.options' size='mini' border>
		    		<el-table-column label='' width='60' align='center'>
		    			<template slot-scope='{$index,row}'>
		    				{{$index+1}}
		    			</template>
		    		</el-table-column>
		    		<el-table-column prop='label' label='label' width='60' align='center'></el-table-column>
		    		<el-table-column label=''>
		    			<template slot-scope='scope'>
		    				<el-input v-model='scope.row.name'></el-input>
		    			</template>
		    		</el-table-column>
		    		<el-table-column label=''  width='60' align='center'>
		    			<template slot-scope='scope'>
		    				<el-input v-model='scope.row.score'></el-input>
		    			</template>
		    		</el-table-column>
		    		<el-table-column label=''  width='60' align='center'>
		    			<template slot-scope='scope'>
		    				<i class="fa fa-trash"></i>
		    			</template>
		    		</el-table-column>
		    	</el-table>
		    	<div style='text-align:right;'>
		    		<el-button :disabled='questionDialog.form.options.length>=5' size='mini' @click='addOption'><i class="fa fa-plus"></i></el-button>
		    	</div>
		     
		    </el-form-item>
		  </el-form>
		  <div slot="footer" class="dialog-footer">
		    <el-button size='mini' @click='closeQuestionDialog'> </el-button>
		    <el-button size='mini' type="primary" @click='saveOrUpdateQuestion'> </el-button>
		  </div>
		</el-dialog>
		<!--  -->
	</div>
</template>

<script>
	import getAxios from '@/http/getAxios'
	//post
	let axios = getAxios('array');
	// arrayFormat:repeat 	
	let axios2 = getAxios();
	export default {
		data(){
			return {
				loading:false,
				ids:[],
				questions:[],
				rules:{},
				questionDialog:{
					title:'',
					visible:false,
					form:{
						options:[]
					}
				}
			}
		},
		created(){
			this.findAllQuestions();
		},
		methods:{
			deleteQuestion(id){
				this.$confirm('?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(()=>{
        	axios.get('/question/deleteQuestionById?id='+id)
        	.then(({data:result})=>{
        		this.$notify.success({
        			title:'',
        			message:result.message
        		});
        		this.findAllQuestions();
        	})
        	.catch(()=>{
        		this.$notify.error({
        			title:'',
        			message:''
        		});
        	})
        })
			},
			toUpdateQuestion(row){
				this.questionDialog.title = '';
				this.questionDialog.visible = true;
				this.questionDialog.form = row;
			},
			addOption(){
				let option = {};
				let label = ''
				let score = 0;
				switch(this.questionDialog.form.options.length){
					case 0:
						label='A'; score = 5;
						break;
					case 1:
						label='B'; score = 4;
						break;
					case 2:
						label='C'; score = 3;
						break;
					case 3:
						label='D'; score = 2;
						break;
					case 4:
						label='E'; score = 1;
						break;
				}
				option.label = label;
				option.score = score;
				this.questionDialog.form.options.push(option);
			},
			saveOrUpdateQuestion(){
				// 
				if(this.questionDialog.form.id){
					if(this.questionDialog.form.questionType==''){
						delete this.questionDialog.form.options;
					}
				}
				// 
				axios.post('/question/saveOrUpdateQuestion',this.questionDialog.form)
				.then(({data:result})=>{
					if(result.status == '200'){
						this.$notify.success({title:'', message:result.message })
						this.closeQuestionDialog();
						this.findAllQuestions();
					} else {
						this.$notify.error({title:'', message:result.message })
					}
				})
				.catch((error)=>{
					this.$notify.error({title:'', message:'' })
				})
			},
			closeQuestionDialog(){
				this.questionDialog.visible = false;
			},
			findAllQuestions(){
				this.loading = true;
				axios.get('/question/findAllQuestionVM')
				.then(({data:result})=>{
					this.questions = result.data;
				})
				.catch(()=>{
					this.$notify.error({
						title:'',
						message:''
					})
				})
				.finally(()=>{
					this.loading = false;
				});
			},
			toAddQuestion(){
				this.questionDialog.title = '';
				this.questionDialog.visible = true;
				this.questionDialog.form = {
					options:[]
				};
			},
			batchDeleteQuestion(){
				this.$confirm('?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(()=>{
        	axios2.post('/question/batchDeleteQuestion',{ids:this.ids})
        	.then(({data:result})=>{
        		this.$notify.success({
        			title:'',
        			message:result.message
        		});
        		this.findAllQuestions();
        	})
        	.catch(()=>{
        		this.$notify.error({
        			title:'',
        			message:''
        		});
        	})
        })
			}
		}
	}
</script>

<style>
	.btns {
		margin-bottom: .5em;
	}
 	.question-list {
 		border-top: 1px solid #ededed;
 	}
	.question {
		padding: .5em 2em;
		border-bottom: 1px solid #ededed;
		position: relative;
	}
	.question > .question-title {
		line-height: 2.5em;
		color: #333;
	}
	.question > .question-options {
		padding-left: 2em;
	}
	.question > .question-options li{
		line-height: 2em;
	}
	.question > .question-btns {
		position: absolute;
		top: .5em;
		right: 2em;
	}

	i.fa {
		cursor: pointer;
		padding: 0 .3em;
	}
	i.fa.fa-trash {
		color: #F56C6C;
	}
</style>














